import React from 'react';
import './styles.scss';

const Card = ({ cards }) => (
	<React.Fragment>
		{cards.map(
			(
				{ img_url, img_alt, tag_color, tag, article_title, article_url },
				index
			) => (
				<div className="card" key={index}>
					<img src={img_url} alt={img_alt} className="card__img" />
					<div className="card__description">
						<div className="card__eyebrow">
							<div className={`card__tag card__tag-${tag_color}`}>
								{tag}
							</div>
						</div>
						<a href={article_url}>
							<h3 className="card__title">{article_title}</h3>
							<div className="card__learnMore">
								<div>Learn More →</div>
                            </div>
                        </a>
                    </div>
				</div>
			)
		)}
	</React.Fragment>
);

export default Card