import React from "react";
import { BrowserRouter as Router, Route, Switch, Link, useParams, NavLink } from "react-router-dom";
import FullNameLogo from "../logos/fullNameLogo"

const Menu = ({ navs, className }) => (
	<React.Fragment>
		<Router>
			<div className={className}>
				<ul className="menu__links">
					{navs.map(({ displayText, path }, index) => (
						<li className="menu__link" key={index}>
							<Link to={path}>
								<span className="menu__text">
									{displayText}
									<span>↗</span>
								</span>
							</Link>
						</li>
					))}
				</ul>
				<div className="menu__bottom">
					<FullNameLogo className="text-logo"></FullNameLogo>
				</div>
			</div>
		</Router>
	</React.Fragment>
);

export default Menu;
